@font-face {font-family: Lexend; font-weight: 100; src: url('Fonts/Lexend/Lexend-Thin.ttf');}
@font-face {font-family: Lexend; font-weight: 300; src: url('Fonts/Lexend/Lexend-Light.ttf');}
@font-face {font-family: Lexend; font-weight: 400; src: url('Fonts/Lexend/Lexend-Regular.ttf');}
@font-face {font-family: Lexend; font-weight: 500; src: url('Fonts/Lexend/Lexend-Medium.ttf');}
@font-face {font-family: Lexend; font-weight: 600; src: url('Fonts/Lexend/Lexend-SemiBold.ttf');}
@font-face {font-family: Lexend; font-weight: 700; src: url('Fonts/Lexend/Lexend-Bold.ttf');}
@font-face {font-family: Lexend; font-weight: 800; src: url('Fonts/Lexend/Lexend-ExtraBold.ttf');}

@font-face {font-family: Roboto; font-weight: 100; src: url('Fonts/Roboto/Roboto-Thin.ttf');}
@font-face {font-family: Roboto; font-weight: 300; src: url('Fonts/Roboto/Roboto-Light.ttf');}
@font-face {font-family: Roboto; font-weight: 400; src: url('Fonts/Roboto/Roboto-Regular.ttf');}
@font-face {font-family: Roboto; font-weight: 500; src: url('Fonts/Roboto/Roboto-Medium.ttf');}
@font-face {font-family: Roboto; font-weight: 700; src: url('Fonts/Roboto/Roboto-Bold.ttf');}
@font-face {font-family: Roboto; font-weight: 900; src: url('Fonts/Roboto/Roboto-Black.ttf');}
:root {
  --headingFonts: 'Lexend', 'Roboto', sans-serif;
  --textFonts: 'Roboto', sans-serif;
  --purple: #8317FF;
  --darkBlue: #214AFF;
  --lightBlue: #2BD8FF;
  --green: #1FFFAC;
  --yellow: #FFDD01;
  --red: #FF4648;
}
html {
  height: 100%;
  overflow-x: hidden;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
h1 {
  font-family: var(--headingFonts);
  font-weight: 600;
  line-height: 120%;
  font-size: 45px;
  margin: 0;
  letter-spacing: 0.5px;
  color: white;
  text-shadow: 2px 2px var(--darkBlue);
}
h2 {
  font-family: var(--textFonts);
  font-weight: 400;
  line-height: 150%;
  font-size: 22px;
  color: black;
  margin: 50px 0;
}
#root {
  height: 100%;
}
.App {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Header. */

.HeaderCon {
  position: relative;
  width: 100%;
  z-index: 100;
}
.Header {
  display: flex;
  align-items: center;
  width: 900px;
  margin: 0 auto;
  position: relative;
}
.Header img {
  height: 42px;
  padding: 4px 5px;
}
.HeaderTitle {
  margin-right: auto;
  font-size: 150%;
  font-family: var(--headingFonts);
  font-weight: 600;
  text-transform: uppercase;
  padding: 16px 5px;
  color: white;
  font-weight: 500;
  letter-spacing: 0.3px;
}
.HeaderItem {
  padding: 16px 16px;
  cursor: pointer;
  vertical-align: middle;
  color: white;
}

/* Landing. */

.Landing {
  width: 100%;
  height: 600px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.LandingMediaCon {
  height: 500px;
  width: 900px;
  bottom: 0;
  position: absolute;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.LandingImgCon {
  width: 60%;
  height: 88%;
  position: relative;
  display: flex;
  align-items: center;
}
.LandingImg {
  margin: auto;
  height: 140%;
  position: absolute;
  bottom: 0;
  transform: translateX(-50%);
  z-index: -1;
}
.LandingText {
  width: 40%;
  height: 88%;
  position: relative;
}
.LandingTextCentre {
  width: 100%;
  height: 90%;
  margin: auto 0;
}
.SearchBarCon {
  height: 12%;
  width: 900px;
  position: relative;
}
.SearchBar {
  height: 100%;
  width: 100%;
  padding-left: 20px;
  font-size: 20px;
  background-color: white;
  box-shadow: 0px 10px 20px -5px rgba(33,74,255,0.7);
  border-radius: 5px;
  border: 2px solid #8317FF;
  outline: none;
  appearance: none;
}
.SearchBarCon {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.TitleContentCon {
  height: 450px;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.TitleContent {
  height: 350px;
  width: 900px;
  margin: 0 auto;
  bottom: 0;
  position: absolute;
  display: flex;
  align-items: center;
  flex-direction: row;
}
.TitleContentTile {
  height: 330px;
  width: 250px;
  margin: 0 auto;
  background-color: white;
  position: relative;
  box-shadow: 5px 5px 30px 10px rgba(0,0,0,0.06);
}
.TileImg {
  margin: 5% 25%;
  width: 50%;
}
.TileTitle {
  height: 10%;
  width: 80%;
  margin: 0 10%;
  color: #214AFF;
  font-family: var(--headingFonts);
  font-size: 24px;
  font-weight: 500;
  text-align: center;
}
.TileText {
  width: 80%;
  height: 35%;
  margin: 5% 10%;
  text-align: center;
}

/* CTA. */

.CTA {
  width: 100%;
  height: 1000px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.CTACon {
  width: 900px;
  height: 900px;
  position: absolute;
  bottom: 0;
}
.CTAExtensionCon {
  width: 900px;
  height: 400px;
  margin: 0 auto;
  position: relative;
}
.CTASection {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40%;
  position: relative;
  margin-bottom: 10%;
}
.CTAImg {
  width: 50%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.CTAImg img {
  width: 80%;
  height: 80%;
}
.CTAText {
  width: 50%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.CTATextMargins {
  width: 80%;
  height: 80%;
  position: relative;
}
.CTATextHeader {
  font-size: 24px;
  font-weight: 500;
  padding-bottom: 12px;
  color: #214aff;
}
.CTATextBody {
  text-align: justify;
}
.CTATextRight .CTATextHeader {
  direction: rtl;
}

/* Footer */

.FooterCon {
  position: relative;
  width: 100%;
  z-index: 100;
  background: rgb(2,0,36);
  background: linear-gradient(45deg, rgba(2,0,36,1) 0%, rgba(33,74,255,1) 100%);
}
.Footer {
  width: 900px;
  margin: 0 auto;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.FooterLogo {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.FooterLogo img {
  height: 40px;
  padding: 4px 5px;
}
.FooterTitle {
  font-size: 135%;
  font-family: var(--headingFonts);
  font-weight: 600;
  text-transform: uppercase;
  padding: 16px 5px;
  color: white;
  font-weight: 500;
  letter-spacing: 0.3px;
}
.FooterLinks {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.FooterLinks ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.FooterLinks li {
  float: left;
  padding: 12px;
  color: white;
}
.FooterDisclaimer {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.FooterDisclaimer p{
  color: white;
}

/* About Us */

.ContentTitleCon {
  width: 100%;
  height: 300px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ContentTitle {
  height: 100%;
  width: 900px;
  bottom: 0;
  position: absolute;
  display: flex;
  justify-content: center;
}
.BannerImg {
  margin: auto;
  position: absolute;
  bottom: 150px;
  z-index: -1;
}
.ContentTitle h1 {
  margin-top: 50px;
}